.finance-top__title{
  background: linear-gradient(to right, #106AB6 0%, #9D3ADF 100%);
  background: -webkit-linear-gradient(left, #106AB6 0%, #9D3ADF 100%);
  padding:2.3vw 10vw;
}

.finance-top__img{
  margin:4.2vw auto 3.9vw;
  width:100%;
}

.each-course-top__icon{
  display:flex;
  justify-content:center;
  align-items:center;

  .finance-top__icon__icon{
    width:6.8vw;
  }

  .each-course-top__icon__text{
    font-size:2.1vw;
    padding-left:3.2vw;
    letter-spacing:0.24em;
  }
}